    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
} */

/* .hero-section .call-to-action button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 32px 64px;
    width: auto;
} */

.hero-section .main .call-to-action {
    display: flex;
    justify-content: center;
}

.hero-section .main .call-to-action button {
    width: 350px;
}

.hero-section .primary-button-text {
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    text-transform: uppercase;
    color: var(--white);
}

.hero-section .secondary-button-text {
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    text-transform: uppercase;
    /* color: var(--dark-blue); */
    width: 201px;
}

.hero-section .shine {
    position: absolute;
}

.hero-section .cloud-sm {
    position: absolute;
    top: 120px;
    animation-name: cloudSm;
    animation-duration: 25s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    z-index: 2;
}

@keyframes cloudSm {
    from {
        right: 0vw;
    }
    to {
        right: 80vw;
    }
}

.hero-section .cloud-md {
    position: absolute;
    bottom: -100px;
    opacity: 0;
    animation-name: cloudMd;
    animation-duration: 15s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    z-index: 4;
}

@keyframes cloudMd {
    from {
        right: 40vw;
        opacity: 1;
    }
    to {
        right: 80vw;
        opacity: 1;
    }
}

.eden-word {
    width: 420px;
}

.eden-word span {
    display: block;
    width: 420px;
    overflow: hidden;
    animation: typing 1s linear;
}

@media screen and (max-width: 768px) {
    .eden-word {
        width: 154px;
    }

    .eden-word span {
        width: 154px;
    }
}

@keyframes typing {
    from {
        width: 0;
    }
}

@media screen and (max-width: 768px) {
    .hero-section {
        margin-top: 60px;
        margin-bottom: 80px;
        justify-content: center;
    }

    .img-gif {
        width: 100%;
    }

    .hero-section .main {
        flex-direction: column;
    }

    .hero-section .main .content {
        align-items: center;
    }

    .hero-section .call-to-action {
        flex-direction: column;
    }

    .hero-section .call-to-action button:nth-child(1) {
        margin-bottom: 12px;
    }

    .hero-section .call-to-action button {
        min-width: 300px;
    }

    .hero-section .main .content .logo {
        align-items: center;
    }

    .hero-section .main .image {
        margin: 0px auto 55px auto;
    }

    .hero-section .second-line {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .hero-section .of {
        font-size: 40px;
        line-height: 89%;
    }

    .hero-section .horizontal-line {
        width: 50px;
    }

    .hero-section .cloud-md {
        top: 420px;
    }

    .hero-section .cloud-sm {
        top: 730px;
    }

    .hero-section .main .call-to-action {
        flex-direction: column;
        /* padding: 0 24px; */
        align-items: center;
    }

    /* .hero-section .call-to-action button {
          width: 100%;
      } */
    .hero-section .main .call-to-action button {
        width: 300px;
        font-size: 20px;
    }

    .hero-section .main .call-to-action > button:first-child {
        margin-bottom: 16px;
    }
}
