    /* padding: 70px 160px; */
    background: url('../../../images/background/faq-bg.png') center center;
    background-size: cover;
    overflow: hidden;
    max-width: 100vw;
}

#Faq .heading-section {
    display: flex;
    justify-content: space-between;
    padding: 32px;
}

