#InviteUser {
    background-image: url("../../../images/background/footer-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
    height: 29vw;
    padding-bottom: 550px;
}

#InviteUser .main .header {
    text-align: center;
    max-width: 981px;
    margin-left: auto;
    margin-right: auto;
    color: var(--dark-blue);
    padding: 0px 4px;
}

#InviteUser .main .call-to-action {
    display: flex;
    justify-content: center;
    padding: 0 32px;
}

#InviteUser .main .call-to-action button {
    width: 350px;
}

#InviteUser .main .call-to-action button:nth-of-type(1) {
    margin-right: 32px;
}

@media screen and (max-width: 768px) {
    #InviteUser {
        background-size: contain;
        height: auto;
        padding-bottom: 200px;
    }

    #InviteUser .main .call-to-action {
        flex-direction: column-reverse;
    }

    #InviteUser .main .call-to-action button {
        width: auto;
        font-size: 20px;
    }

    #InviteUser .main .call-to-action button:nth-of-type(1) {
        margin-right: 0px;
    }
}
