#Pitch {
    margin-bottom: 260px;
}

#Pitch .main .header {
    margin-bottom: 140px;
}

.line-1 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 32px;
}

#Pitch .main .header .line-1 div:nth-of-type(1) {
    margin-right: 32px;
    margin-top: 14px;
}

#Pitch .main .header .line-1 div:nth-of-type(1) span {
    color: var(--red);
}

#Pitch .promise-highlight {
    color: var(--white);
    background-color: var(--pink);
    border-radius: 81px;
    padding: 40px 60px 27px;
}

#Pitch .main .header .line-1 .promise-highlight span {
    color: var(--white);
}

.line-2 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 32px;
}

#Pitch .freedom-highlight {
    display: flex;
    align-content: center;
    border: 6px solid var(--black);
    border-radius: 81px;
    padding: 40px 60px 27px;
}

#Pitch .main .header .line-2 div:nth-of-type(2) {
    margin-left: 32px;
    margin-top: 14px;
}

#Pitch .redText {
    color: var(--red);
    display: inline-block;
}

#Pitch .main .header .line-3 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 32px;
}

#Pitch .economic-highlight {
    background-color: var(--blue);
    border-radius: 81px;
    padding: 40px 60px 27px;
    color: var(--white);
    margin: 0px 140px;
}

#Pitch .info {
    display: flex;
    justify-content: center;
    align-items: start;
    padding: 0px 160px;
}

#Pitch .main .info div {
    /* width: 520px; */
    /* margin-right: 60px; */
}

@media screen and (max-width: 768px) {
    #Pitch {
        margin-bottom: 120px;
    }

    .line-1 {
        flex-direction: column;
    }

    .line-2 {
        margin-bottom: 16px;
    }

    #Pitch .promise-highlight {
        border-radius: 81px;
        padding: 14px 21px;
    }

    #Pitch .freedom-highlight {
        border: 2px solid var(--black);
        padding: 14px 21px;
    }

    #Pitch .economic-highlight {
        padding: 14px 21px;
        margin: 0px 3px;
    }

    #Pitch .info {
        flex-direction: column;
        padding: 0px 16px;
    }

    #Pitch .info div {
        text-align: left;
    }
}
