#community {
    padding: 0 130px;
    margin-bottom: 210px;
    text-align: center;
}

#community .heading {
    margin-bottom: 101px;
    color: var(--dark-blue);
    text-align: center;
}

#community .content {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
}

#community .content > div:first-child {
    position: relative;
    margin-bottom: 0px;
    text-align: left;
}

#community .content > div:first-child img:first-child {
    position: absolute;
    right: 20px;
    top: -50px;
    z-index: 1;
}

#community .content > div:first-child img:nth-child(2) {
    position: relative;
    z-index: 2;
}

#community .content > div:first-child img:nth-child(3) {
    position: absolute;
    bottom: -100px;
    left: 97px;
    z-index: 3;
}

#community .content .action-button-container {
    display: flex;
    flex-direction: row;
}

#community .communitycard {
    display: flex;
    background-color: #fff;
    padding: 16px;
    align-items: center;
    border-radius: 12px;
    padding: 18px;
    margin-bottom: 24px;
}

.communitycard .community-icon {
    margin-right: 10px;
}

.communitycard .community-text {
    font-family: var(--secondarybold-font);
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    color: #28292e;
}

#community .primary-button-text {
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    text-transform: uppercase;
    color: var(--white);
    width: 384px;
    margin-right: 30px;
}

#community .secondary-button-text {
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    text-transform: uppercase;
    /* color: var(--dark-blue); */
    width: 201px;
}

#community .heading {
    margin-bottom: 0 !important;
}

#community .sub-heading {
    margin-bottom: 30px;
}

@media screen and (max-width: 768px) {
    #community {
        padding: 0px 16px;
        margin-bottom: 100px;
    }

    #community .content {
        flex-direction: column;
    }

    #community .communitycard {
        padding: 12px 16px;
    }

    #community .heading {
        margin-bottom: 0 !important;
    }

    #community .sub-heading {
        margin-bottom: 30px;
    }

    #community .primary-button-text {
        width: auto;
        /* height: auto; */
        font-size: 20px;
        line-height: 20px;
        padding: 16px;
        margin-right: 16px;
        margin-left: 16px;
        margin-bottom: 16px;
    }

    #community .secondary-button-text {
        width: auto;
        /* height: auto; */
        font-size: 20px;
        line-height: 20px;
        padding: 16px;
        margin-right: 16px;
        margin-left: 16px;
    }

    .communitycard .community-text {
        text-align: left;
        font-size: 14px;
    }

    #community .content > div:first-child {
        position: relative;
        margin-bottom: 64px;
        text-align: center;
    }

    #community .content > div:first-child img:first-child {
        left: 10px;
        top: 30px;
    }

    #community .content > div:first-child img:nth-child(3) {
        position: absolute;
        bottom: 0px;
        left: 170px;
        z-index: 3;
    }

    #community .content .action-button-container {
        flex-direction: column;
    }
}
