#Profile {
    background-image: url("../../../images/background/faq-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
    padding-bottom: 200px;
}

#Profile .divider {
    width: 100%;
}

/*profile-section*/
.profile-section {
    padding: 32px;
}

.profile {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.profile .profile-info {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: end;
}

.profile-image img {
    height: 160px;
    width: 160px;
    border-radius: 50%;
    border: 2px solid white;
    margin-right: 24px;
}

.profile-details {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.profile-details .profile-name {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.profile-details .profile-name .address-button {
    font-size: 12px;
    margin-left: 12px;
}

.profile-details .name {
    font-size: 54px;
}

.profile-details .profile-stats {
    display: flex;
    align-items: center;
    /*margin-top: 8px;*/
}

.profile-details .profile-stats img {
    width: 16px;
    height: 16px;
    margin-right: 12px;
}

.profile-details .profile-stats .profile-nft-count {
    margin-right: 12px;
}

.action-button .mint-nft {
    height: 42px;
    padding: 0 32px;
    border-width: 2px;
    width: 100%;
    transition: 0.2s ease-in-out;
    font-size: 16px;
}

/*collection-section*/

.collection-section {
    padding: 0 32px;
}

.collection-section .head {
    display: flex;
    justify-content: space-between;
    padding: 32px 0;
}

.collection-section .right-head {
}

.collection-section .right-head .fuse-nft-btn {
    height: 42px;
    padding: 0 32px;
    border-width: 2px;
    width: 100%;
    transition: 0.2s ease-in-out;
    font-size: 16px;
}

/*nft-collection-section*/
.nft-card {
    position: relative;
    border-radius: 12px;
    overflow: hidden;
    cursor: pointer;
    background: whitesmoke;
    box-shadow: 2px 2px 18px rgb(0 0 0 / 10%);
}

.nft-card .nft-image img {
    width: 100%;
}

.nft-card .nft-details {
    padding: 12px;
}

.nft-card .nft-details-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nft-card .nft-details .nft-id {
    font-weight: bold;
}

.nft-card .nft-details .rarity-tags {
    font-size: 12px;
    background: var(--pink);
    padding: 4px 12px;
    border-radius: 12px;
    color: var(--white);
}

.nft-card .nft-details .maha-value {
}

.evolve-nft-btn {
    position: absolute;
    bottom: -32px;
    text-align: center;
    width: 100%;
    padding: 8px 0;
    background: var(--dark-blue);
    color: white;
    transition: 0.2s ease-in-out;
    z-index: 99;
}

.nft-card:hover > .evolve-nft-btn {
    bottom: 0;
}

@media (max-width: 768px) {
    .profile {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .profile .profile-info {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .profile-details .name {
        text-align: center;
    }

    .profile-details .profile-stats {
        justify-content: center;
    }

    .profile-image img {
        margin-right: 0;
        margin-bottom: 24px;
    }

    .profile-details {
        margin-bottom: 24px;
    }
}
