#utility {
    background: url("../../../images/background/homapage-light.png") center center;
    background-size: cover;
    overflow: hidden;
    max-width: 100vw;
}

.dark-blue-text {
    color: var(--dark-blue);
}

.knowmore {
    font-family: var(--secondary-font);
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 170%;
    text-decoration-line: none;
    color: #1d97de;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .knowmore {
        font-size: 16px;
    }
}
