.maharaja-slideshow {
    height: 320px;
    max-width: 1000%;
    margin-bottom: 132px;
    position: relative;
    overflow: hidden;
    font-size: 0;
}

.mover-1 {
    height: 300px;
    width: max-content;

    position: absolute;
    overflow-x: hidden;
    top: 0;
    left: 0;

    animation: moveSlideshow 10s linear infinite;
}

.mover-1 img {
    margin-left: 6px;
    margin-right: 6px;
}

@keyframes moveSlideshow {
    0% {
        transform: translateX(0%);
    }
    100% {
        transform: translateX(-70%);
    }
}
