#About {
    margin-top: 136px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 195px;
}

#About .about .main {
    z-index: 1;
    position: relative;
}

#About .header {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
}

#About .header > img:nth-of-type(1) {
    margin-right: 32px;
}

#About .header > img:nth-of-type(2) {
    margin-left: 32px;
}

#About .line-1 {
    text-align: center;
}

#About .line-1 > span {
    color: var(--blue);
}

#About .line-2 > span {
    color: var(--red);
}

#About .info {
    max-width: 920px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

@media screen and (max-width: 768px) {
    #About {
        margin-top: 109px;
        margin-bottom: 64px;
    }

    #About .line-2 {
        text-align: center;
    }

    #About .header {
        justify-content: center;
    }
}
