#gallery {
    background-image: url("../../../images/background/faq-bg.png");
    min-height: 100vw;
    background-repeat: repeat;
    background-size: cover;
    padding-bottom: 200px;
}

#gallery > div:first-child {
    padding: 43px;
}

#gallery .heading-section {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
}

#gallery label {
    width: auto;
    background-color: #fff;
}

#gallery .heading-section .heading-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 16px;
    background-color: #1d97de;
    border-radius: 6000px;
}

#gallery .heading-section .heading-btn .heading-btn-text {
    color: #fff;
    margin-right: 8px;
    font-family: var(--secondary-font);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    text-align: center;
}

#gallery .gallery-filter-container {
    display: flex;
}

#gallery .filterCard {
    padding: 16px;
}

#gallery .searchbar {
    padding: 16px;
    margin-bottom: 19px !important;
}

#gallery .nft-count {
    font-family: var(--secondarybold-font);
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    color: #1d97de;
}

#gallery .sort-by {
    font-family: var(--secondary-font);
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 120%;
    color: #c6cbd9;
}

#gallery .accordion-title {
    font-family: var(--secondarybold-font);
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 120%;
    color: #28292e;
}

#gallery .accordion-option {
    font-family: var(--secondarybold-font);
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 120%;
    text-align: center;
    color: #28292e;
}

#gallery .accordion-option-count {
    font-family: var(--secondary-font);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 120%;
    color: #bdbdbd;
    margin-left: 4px;
}

.filter-checkbox {
    cursor: pointer;
}

.filter-card-accordion {
    padding-top: 12px;
    flex-direction: column;
}

.filter-bottom-card {
    display: flex;
    flex-direction: column;
    background: #fafafa;
    border-radius: 8px;
    border: 1px solid #f2f3f7;
}

.filter-bottom-card > div:first-child {
    display: flex;
    justify-content: space-between;
    margin-bottom: 12px;
    padding: 8px 10px;
}

.filter-bottom-card > div:last-child {
    display: flex;
    flex-direction: column;
    padding: 8px 10px;
}

.MuiOutlinedInput-input {
    padding: 12px !important;
}

.MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
    border-color: rgba(0, 0, 0, 0.23) !important;
}

.MuiSelect-selectMenu {
    color: #7e7e8f !important;
}

#nft-with-id {
    /* margin-right: 20px; */
    margin-bottom: 36px;
    overflow: hidden;
}

#nft-with-id .nft-image-container {
    overflow: hidden;
    margin-bottom: 8px;
    border-radius: 12px;
    min-height: 120px;
}

#nft-with-id img {
    border-radius: 12px;
    cursor: pointer;
}

#nft-with-id .nft-id {
    font-family: var(--secondarybold-font);
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 120%;
    display: flex;
    align-items: center;
    color: #2e2e3a;
    padding: 0 8px;
}

#nft-with-id .maha-amount {
    margin-top: 2px;
    padding: 0 8px;
    font-family: var(--secondarylight-font);
    font-size: 14px;
}

#filter-sidebar {
    padding: 12px;
    border: 1px solid #bbb;
    border-radius: 5px;
    margin-right: 16px;
    display: flex;
    justify-content: space-between;
}

.grow {
    transition: 0.5s ease;

}

.grow img:hover {
    transform: scale(1.05);
    transition: 0.4s ease-in-out;
}

@media screen and (max-width: 425px) {


    #gallery .gallery-filter-container {
        flex-direction: column;
    }

    #gallery .heading-section .heading-btn {
        width: 174px;
        margin: 0 auto;
    }
}

@media screen and (max-width: 768px) {
    #gallery .heading-section {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    #gallery > div:first-child {
        padding: 64px 16px 64px 16px;
    }

    #gallery .nft-count {
        margin-bottom: 16px;
    }

    #gallery .searchbar {
        padding: 8px;
    }

    #nft-with-id .nft-id {
        justify-content: center;
    }
}
