#single-nft {
    background-image: url('../../../images/background/faq-bg.png');
    min-height: 100vh;
    padding-bottom: 120px;
    background-size: cover;
}

#single-nft .heading-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 42px;
}

#single-nft .heading-section .heading-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 16px;
    background-color: var(--blue);
    border-radius: 60px;
    cursor: pointer;
}

#single-nft .heading-section .heading-btn .heading-btn-text {
    color: var(--white);
    margin-right: 8px;
    font-family: var(--secondary-font), serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    text-align: center;
    margin-left: 4px;
}

#single-nft .divider {
    width: 100%;
}

.nft-details-section {
    padding: 32px 0;
}

.nft-details-section .details-left {
    padding: 0 64px;
}

.nft-details-section .details-left .nft-img img {
    width: 100%;
    border-radius: 12px;
}

.nft-details-section .details-left  .nft-history-disclaimer {
    font-size: 16px;
    text-align: center;
    margin-top: 12px;
}

.nft-details-section .details-right {
    padding: 0 64px 0 0;
}

.details-right section {
    margin-bottom: 24px;
}

.details-right .nft-name {
    font-size: 40px;
}

.details-right .nft-owner a {
    color: var(--blue);
}

.details-right .traits-heading {
    margin-bottom: 24px;
    font-size: 32px;
    text-align: center;
}

.details-right .value-info {
    border-radius: 12px;
    backdrop-filter: blur(10x);
    background-color: rgba(0, 0, 0, 0.05);
    padding: 12px 16px;
}

.details-right .value-info .value-title {
    font-size: 24px;
    font-family: var(--secondarybold-font);
}

.details-right .value-info .title-info {
    font-size: 16px;
    font-family: var(--secondary-font);
}

.details-right .traits-info {
    border-radius: 12px;
    backdrop-filter: blur(10x);
    background-color: rgba(0, 0, 0, 0.05);
    padding: 12px 16px;
}

.details-right .traits-info .traits-title {
    font-size: 16px;
    font-family: var(--secondarybold-font);
}

.details-right .traits-info .title-info {
    font-size: 12px;
    font-family: var(--secondary-font);
}

.evolve-nft, .fuse-nft {
    height: 60px;
    padding: 0 32px;
    border-width: 2px;
    transition: 0.2s ease-in-out;
}

@media (max-width: 768px) {
    #single-nft .heading-section {
        padding: 24px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .nft-details-section .details-left {
        padding: 0 24px;
    }

    .nft-details-section .details-right {
        padding: 0 24px;
    }

    .nft-details-section .details-left .nft-img {
        margin-bottom: 64px;
    }

    #single-nft .heading-section .heading-title .heading-6 {
        font-size: 24px;
    }

    #single-nft .heading-section .heading-btn .heading-btn-text {
        display: none;
    }
}

@media (max-width: 425px) {
    #single-nft .heading-section {
        padding: 12px 0;
    }

    .nft-details-section .details-left .nft-img {
        margin-bottom: 32px;
    }

    .nft-details-section .details-left {
        padding: 0;
    }

    .nft-details-section .details-right {
        padding: 0;
    }

}
