#countdown {
    display: flex;
    width: 100%;
}

#countdown .countdown-internal {
    display: flex;
    align-items: center;
    width: 100%;
}

#countdown .timer {
    color: var(--dark-blue);
    font-size: 20px;
    margin-top: 16px;
    font-weight: bold;
    backdrop-filter: blur(5px);
    background: rgb(255, 255, 255, 0.8);
    padding: 18px 24px;
    border-radius: 12px;
    text-align: center;
}
