html {
    scroll-behavior: smooth;
}

@font-face {
    font-family: "Avander";
    src: local("Avander"), url("./fonts/Avander/Avander.ttf");
}

@font-face {
    font-family: "General Sans";
    src: local("General Sans"), url("fonts/GeneralSans/GeneralSans.otf");
}

@font-face {
    font-family: "General Sans Bold";
    src: local("General Sans"), url("fonts/GeneralSans/GeneralSansMedium.otf");
}

@font-face {
    font-family: "General Sans SemiBold";
    src: local("General Sans"), url("fonts/GeneralSans/GeneralSansSemibold.otf");
}

@font-face {
    font-family: "General Sans Light";
    src: local("General Sans"), url("fonts/GeneralSans/GeneralSansLight.otf");
}

:root {
    --primary-font: "Avander", serif;
    --secondary-font: "General Sans", cursive;
    --secondarybold-font: "General Sans Bold";
    --secondarylight-font: "General Sans Light";
    --pink: #ee8cb1;
    --blue: #1d97de;
    --dark-blue: #2e4d6e;
    --grey: #28292e;
    --light-grey: #535353;
    --red: #df3e00;
    --black: #000000;
    --white: #ffffff;
}

* {
    padding: 0;
    margin: 0;
    font-family: Avander, serif;
    color: var(--grey);
}

body {
    overflow-x: hidden;
    height: auto;
}

a {
    text-decoration: none;
}

