}

.d-flex-justify-between {
    justify-content: space-between;
}

.primary-button {
    height: 80px;
    background: var(--dark-blue);
    border: 4px solid var(--dark-blue);
    border-radius: 40px;
    width: 100%;
    transition: 0.2s ease-in-out;
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    color: var(--white);
    cursor: pointer;
}

.primary-button:hover {
    background: #243d57;
}

.primary-button:disabled {
    background: #d9d9d9;
    border-color: #d9d9d9;
    color: #9dacbd;
    cursor: default;
}

.secondary-button {
    height: 80px;
    border: 3px solid var(--dark-blue);
    border-radius: 40px;
    width: 100%;
    transition: 0.2s ease-in-out;
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    color: var(--dark-blue);
    cursor: pointer;
    background-color: transparent;
}

.secondary-button:hover {
    background: #243d57;
    color: #ffffff;
}

.secondary-button:disabled {
    cursor: no-drop;
    border-color: #2e4d6e30;
    color: #2e4d6e30;
    background: none;
}

.pointer {
    cursor: pointer;
}

