#rarity {
    padding: 0 130px;
    margin-bottom: 288px;
}

#rarity .heading {
    color: #2e4d6e;
    text-align: center;
    /* margin-bottom: 127px; */
}

#rarity .sub-heading {
    margin-bottom: 32px;
}

#rarity .nft-examples-container {
    margin-bottom: 120px;
}

#rarity .nft-examples-container > div {
    display: flex;
    justify-content: center;
    align-items: center;
}

#rarity .nft-examples-container > div:last-child {
    margin-right: 0px;
}

#rarity .nft-examples-container .rarity-img {
    width: 250px;
    height: 250px;
    margin-bottom: 12px;
    border-radius: 12px;
}

#rarity .nft-examples-container .rarity-img-text {
    font-family: var(--secondary-font);
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    text-transform: uppercase;
    color: #28292e;
}

#rarity .primary-button-text {
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    text-transform: uppercase;
    color: var(--white);
    width: 384px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 24px auto 0px auto;
}

@media screen and (max-width: 768px) {
    #rarity {
        padding: 0 16px;
        margin-bottom: 88px;
    }

    #rarity .heading {
        margin-bottom: 32px;
    }

    #rarity .nft-examples-container {
        margin-bottom: 32px;
    }

    #rarity .nft-examples-container .rarity-img {
        width: 144px;
        height: 144px;
        /* margin-right: 10px; */
    }

    #rarity .nft-examples-container > div {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }

    #rarity .nft-examples-container .rarity-img-text {
        font-family: var(--secondary-font);
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 120%;
        color: #2e2e3a;
        text-transform: uppercase;
    }

    #rarity .primary-button-text {
        width: 273px;
        font-size: 20px;
        line-height: 20px;
    }
}
