#stake-gov {
    padding: 0 130px;
    margin-bottom: 300px;
    display: flex;
    justify-content: space-between;
    position: relative;
}

#stake-gov .heading {
    margin-bottom: 16px;
    color: var(--dark-blue);
    text-align: left;
}

#stake-gov .sub-heading {
    margin-bottom: 24px;
    color: var(--light-grey);
    text-align: left;
    width: 485px;
}

#stake-gov .front_chalice2 {
    position: absolute;
    right: -24px;
    bottom: -280px;

}

#stake-gov .button-text {
    font-family: 'Avander';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 32px;
    text-transform: uppercase;
    color: var(--white);
    padding: 32px 64px;
    margin: 0px 0 16px 0;
    width: auto;
    display: flex;
    align-items: center;

}

#stake-gov > div:first-child {
    margin-right: 183px;
    margin-bottom: 0px;
}

@media screen and (max-width: 768px) {
    #stake-gov {
        padding: 0 16px;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-bottom: 128px;
    }

    #stake-gov .heading {
        text-align: center;
    }

    #stake-gov .sub-heading {
        text-align: center;
        width: auto;
    }

    #stake-gov .button-text {
        width: auto;
        padding: 16px 64px;
        font-size: 20px;
        line-height: 20px;
        margin: 0 auto 8px auto;
        display: flex;
        align-items: center;
    }

    #stake-gov > div:first-child {
        margin-right: 0px;
        margin-bottom: 129px;
    }
}
