#Footer {
    position: fixed;
    z-index: 99;
    bottom: 0;
    width: 100vw;
    box-shadow: 0 -24px 56px rgba(0, 0, 0, 0.20);
}

#Footer .main {
    max-width: 1440px;
    height: 120px;
    margin: 0 auto;
}

.social-media {
    display: flex;
    justify-content: center;
    align-items: center;
}

#Footer .main .header-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 120px;
    padding: 0 72px;
}

#Footer .main .header-wrapper .header {
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 49px;
    line-height: 49px;
    flex: 1;
}

#Footer .main .header-wrapper .menu {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

#Footer .main .header-wrapper .menu-img {
    align-self: flex-end;
    margin-bottom: -12px;
}

#Footer .main .header-wrapper .menu-img img {
    max-height: 200px;
    max-width: 200px;
}

#Footer .main .header-wrapper .menu .menu-items {
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
}

#Footer .main .header-wrapper .menu .star-divider {
    margin-left: 16px;
    margin-right: 16px;
}

@media screen and (max-width: 768px) {
    #Footer .main {
        height: 46px;
    }

    #Footer .main .header-wrapper {
        height: 0px;
        padding: 0px;
        height: 46px;
    }

    #Footer .main .header-wrapper .header {
        font-size: 17.7576px;
        line-height: 18px;
        padding: 15px;
    }
}
