#boost-reward {
    padding: 0 230px;
    margin-bottom: 240px;
    text-align: center;
}

#boost-reward .farming-highlight {
    display: flex;
    align-content: center;
    background-color: var(--blue);
    border-radius: 81px;
    padding: 40px 60px 27px;
    margin-bottom: 32px;
    color: #fff;
    justify-content: center;
}

#boost-reward .heading {
    color: var(--dark-blue);
    margin-bottom: 10px;
}

@media screen and (max-width: 768px) {
    #boost-reward {
        padding: 0px;
        margin-bottom: 128px;
    }

    #boost-reward .farming-highlight {
        font-family: var(--primary-font);
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 100%;
        display: flex;
        align-items: center;
        text-transform: uppercase;
        color: var(--white);
        display: flex;
        align-content: center;
        background-color: var(--blue);
        border-radius: 81px;
        padding: 14px 16px;
        margin-bottom: 32px;
        justify-content: center;
    }

    #boost-reward .sub-heading {
        color: #1C1C1C;
        padding: 0px 16px;
    }
}
