#mint-with {
    text-align: center;
    position: relative;
    margin-bottom: 260px;
}

#mint-with .front_spear1 {
    position: absolute !important;
    left: -80px !important;
}

#mint-with .front_spear2 {
    position: absolute !important;
    right: -80px !important;
}

#mint-with .mint-with-heading {
    padding-top: 60px;
    margin-bottom: 16px;
    color: var(--dark-blue);
    position: relative;
    z-index: 1;
}

#mint-with .sub-heading {
    padding: 0 16px;
    margin-bottom: 32px;
}

#mint-with .eth-or-maha-div {
    display: flex;
    justify-content: center;
    margin-bottom: 32px;
    align-items: center;
    position: relative;
    z-index: 1;
}

#mint-with .eth-highlight {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--pink);
    border-radius: 81px;
    color: var(--white);
    padding: 40px 60px;
    margin-right: 16px;
    width: auto;
}

#mint-with .eth-highlight > div:first-child {
    margin-right: 0px;
}

#mint-with .info {
    z-index: 1;
    position: relative;
}

#mint-with .maha-highlight {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--blue);
    border-radius: 81px;
    color: #fff;
    padding: 40px 60px;
    width: auto;
}

#mint-with .primary-button-text {
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    text-transform: uppercase;
    color: var(--white);
    width: 280px;
}

@media screen and (max-width: 768px) {

    #mint-with {
        padding: 0px 16px;
        margin-bottom: 71px;
    }

    #mint-with .eth-highlight {
        padding: 15px 16px;
        font-size: 24px !important;
    }

    #mint-with .maha-highlight {
        padding: 15px 16px;
        font-size: 24px;
    }

    #mint-with .primary-button-text {
        font-size: 20px;
    }

    #mint-with .eth-highlight > div:first-child {
        margin-right: 5px;
    }
}
