#utility-hero-section {
    padding-top: 200px;
    margin-bottom: 144px;
}

#utility-hero-section .heading {
    z-Index: 1;
    position: relative;
    text-align: center;
    margin-bottom: 8px;

}

#utility-hero-section .sub-heading {
    text-align: center;
    font-family: var(--secondarybold-font);
    position: relative;
    z-index: 1;
    width: 799px;
    margin: 0 auto 24px auto;
}

#utility-hero-section .primary-button-text {
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    text-transform: uppercase;
    color: var(--white);
    width: 295px;
    margin: 0 auto 320px auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

#utility-hero-section #HeroSectionCard {
    position: relative;
    /* margin-bottom: 300px; */
    width: 400px;
}


#utility-hero-section #HeroSectionCard .cardheading {
    font-family: var(--primary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
    line-height: 120%;
    color: #2E4D6E;
    margin: 10px 0 10px 0;
}

#utility-hero-section #HeroSectionCard .cardsubheading {
    color: var(--secondarylight-font);
    width: 250px;

}


#utility-hero-section .hero-cloudlight-sm {
    position: absolute;
    top: 270px;
    left: 0;
    z-index: 1;
}

#utility-hero-section .hero-cloud-sm {
    position: absolute;
    top: 270px;
    right: 170px;
    z-index: 10;
}

#utility-hero-section .hero-card-parent {
    justify-content: center;
    padding: 0px 16px;
    display: flex;
    flex-direction: row;
}

@media screen and (max-width: 768px) {
    #utility-hero-section {
        padding-top: 128px;
        margin-bottom: 144px;
    }

    #utility-hero-section .sub-heading {
        color: #656565;
        width: 328px;
    }

    #utility-hero-section .primary-button-text {
        font-size: 20px;
        line-height: 20px;
        width: 267px;
        margin: 0 auto 120px auto;

    }

    #utility-hero-section .hero-cloudlight-sm {
        top: 250px;
    }

    #utility-hero-section .hero-cloud-sm {
        top: 50px;
        right: 0px;
    }

    #utility-hero-section #HeroSectionCard {
        width: auto;
    }

    #utility-hero-section #HeroSectionCard .cardheading {
        font-size: 32px;
        text-align: center;
    }

    #utility-hero-section #HeroSectionCard .cardsubheading {
        text-align: center;
        width: auto;
        margin-bottom: 24px;
    }

    #utility-hero-section .hero-card-parent {
        flex-direction: column;
    }
}

